import { useEffect, useCallback, useRef } from 'react';

interface KeyboardShortcutsConfig {
    videoRef: React.RefObject<HTMLVideoElement | null>;
    isPlaying: boolean;
    onTogglePlay: () => void;
    onSeek: (time: number) => void;
    onSplitAtPlayhead?: () => void;
    onUndo?: () => void;
    onDeleteSelected?: () => void;
    maxDuration: number;
    segmentStart: number;
    segmentEnd: number;
}

/**
 * Final Cut Pro 
 * 
 * :
 * - Space: /
 * - K: 
 * - L: 
 * - J: 
 * 
 * :
 * -  / : 1 1/30 
 * - Shift +  / : 1
 * - Home / End: /
 * 
 * :
 * - B / Cmd+B: Blade
 * - Delete / Backspace: 
 * - Cmd+Z: 
 */
export function useKeyboardShortcuts({
    videoRef,
    isPlaying,
    onTogglePlay,
    onSeek,
    onSplitAtPlayhead,
    onUndo,
    onDeleteSelected,
    maxDuration,
    segmentStart,
    segmentEnd,
}: KeyboardShortcutsConfig) {
    const playbackRateRef = useRef(1);
    const jklModeRef = useRef<'forward' | 'backward' | 'paused'>('paused');

    const getCurrentTime = useCallback(() => {
        return videoRef.current?.currentTime ?? 0;
    }, [videoRef]);

    const setPlaybackRate = useCallback((rate: number) => {
        if (videoRef.current) {
            videoRef.current.playbackRate = Math.abs(rate);
            playbackRateRef.current = rate;
        }
    }, [videoRef]);

    const handleKeyDown = useCallback((e: KeyboardEvent) => {
        // 
        if (
            e.target instanceof HTMLInputElement ||
            e.target instanceof HTMLTextAreaElement ||
            e.target instanceof HTMLSelectElement
        ) {
            return;
        }

        const video = videoRef.current;
        if (!video) return;

        const currentTime = getCurrentTime();
        const frameStep = 1 / 30; // 
        const bigStep = 1; // 1

        switch (e.code) {
            // ==========  ==========
            case 'Space':
                e.preventDefault();
                onTogglePlay();
                break;

            case 'KeyK':
                // K = 
                e.preventDefault();
                if (isPlaying) {
                    video.pause();
                }
                jklModeRef.current = 'paused';
                setPlaybackRate(1);
                break;

            case 'KeyL':
                // L = 
                e.preventDefault();
                if (jklModeRef.current === 'forward') {
                    // 1x  2x  4x  8x
                    const newRate = Math.min(playbackRateRef.current * 2, 8);
                    setPlaybackRate(newRate);
                } else {
                    jklModeRef.current = 'forward';
                    setPlaybackRate(1);
                }
                if (video.paused) {
                    video.play().catch(() => { });
                }
                break;

            case 'KeyJ':
                // J =  video 
                // 
                e.preventDefault();
                if (jklModeRef.current === 'backward') {
                    // 
                    onSeek(Math.max(0, currentTime - 0.5));
                } else {
                    jklModeRef.current = 'backward';
                    onSeek(Math.max(0, currentTime - 0.1));
                }
                break;

            // ==========  ==========
            case 'ArrowLeft':
                e.preventDefault();
                if (e.shiftKey) {
                    // Shift +  = 
                    onSeek(Math.max(segmentStart, currentTime - bigStep));
                } else {
                    //  = 
                    onSeek(Math.max(segmentStart, currentTime - frameStep));
                }
                break;

            case 'ArrowRight':
                e.preventDefault();
                if (e.shiftKey) {
                    // Shift +  = 
                    onSeek(Math.min(segmentEnd, currentTime + bigStep));
                } else {
                    //  = 
                    onSeek(Math.min(segmentEnd, currentTime + frameStep));
                }
                break;

            // ==========  ==========
            case 'Home':
                e.preventDefault();
                onSeek(segmentStart);
                break;

            case 'End':
                e.preventDefault();
                onSeek(segmentEnd);
                break;

            // ==========  ==========
            case 'KeyB':
                // B  Cmd+B = Blade
                if (e.metaKey || e.ctrlKey || !e.shiftKey) {
                    e.preventDefault();
                    onSplitAtPlayhead?.();
                }
                break;

            case 'Delete':
            case 'Backspace':
                e.preventDefault();
                onDeleteSelected?.();
                break;

            case 'KeyZ':
                if (e.metaKey || e.ctrlKey) {
                    e.preventDefault();
                    onUndo?.();
                }
                break;

            default:
                break;
        }
    }, [
        videoRef,
        isPlaying,
        onTogglePlay,
        onSeek,
        onSplitAtPlayhead,
        onUndo,
        onDeleteSelected,
        getCurrentTime,
        setPlaybackRate,
        segmentStart,
        segmentEnd,
    ]);

    useEffect(() => {
        window.addEventListener('keydown', handleKeyDown);
        return () => {
            window.removeEventListener('keydown', handleKeyDown);
        };
    }, [handleKeyDown]);

    return {
        currentPlaybackRate: playbackRateRef.current,
    };
}
